    font-size: 2.14285714rem;
    line-height: 40px;
    margin-bottom: 15px;
  }
  h2 {
    font-size: 2rem;
    line-height: 35px;
  }
  h3 {
    font-size: 1.64285714rem;
    line-height: 33px;
  }
  h4 {
    font-size: 1.5rem;
    line-height: 26px;
  }
  h5 {
    font-size: 1.35714286rem;
    line-height: 29px;
  }
  h6 {
    font-size: 1.21428571rem;
    line-height: 27px;
  }
  h1:before,
  h2:before,
  h3:before,
  h4:before,
  h5:before,
  h6:before {
    display: none;
  }
  ol li:not(:last-child) {
    margin-bottom: 20px;
  }
}
