.btn {
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 0.85714286rem;
  font-weight: 400;
  padding: 7px 11px;
  line-height: 1.2em;
  border: 1px solid rgba(36, 57, 112, 0.3);
  border-radius: 50px;
  font-family: 'BeauSans', serif;
  color: #243970;
  transition: all 0.25s ease-in-out;
  background: transparent;
}
.btn__icon {
  margin-left: 14px;
  transition: all 0.25s ease-in-out;
}
.btn__icon * {
  stroke: #fff;
}
.btn__text {
  transition: all 0.25s ease-in-out;
}
.btn_big {
  padding: 13px 19px;
}
.btn_glass-like {
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.1);
}
.btn_glass-like .btn__text {
  color: #fff;
}
.btn_glass-like.btn_dark {
  border: 1px solid rgba(0, 0, 0, 0.3);
  background: rgba(0, 0, 0, 0.1);
}
.btn_glass-like.btn_dark .btn__text {
  color: #fff;
}
.btn_trans {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.53);
  color: white;
  font-weight: 300;
}
.btn_frost {
  border-color: transparent;
  background: rgba(255, 255, 255, 0.3);
  color: #fff;
}
.btn_frost.btn_dark {
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
}
.btn_primary-color {
  border-color: transparent;
  background: #1074b9;
  color: #fff;
}
.btn_primary-color svg.btn__icon.icon-arrow-svg * {
  stroke: #fff;
}
.btn_dark {
  color: #243970;
}
@media (min-width: 1024px) {
  .btn:hover {
    box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  }
  .btn_trans:hover {
    text-shadow: 0px 0px 6px #ffffff;
    box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  }
  .btn_frost:hover {
    background: rgba(255, 255, 255, 0.4);
  }
  .btn_frost:hover.btn_dark {
    background: rgba(0, 0, 0, 0.4);
  }
  .btn_with-arrow:hover .btn__text {
    text-shadow: 0px 0px 6px #ffffff;
  }
  .btn_with-arrow:hover .btn__icon {
    transform: translateX(3px);
  }
}
