:root {
  /** Height for menu items. */
  --mm-spn-item-height: 50px;
  /** Indent for menu items. */
  --mm-spn-item-indent: 20px;
  /** Line height for menu items. */
  --mm-spn-line-height: 24px; }

.mm-spn {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
  overflow: hidden;
  clip-path: inset(0 0 0 0);
  transform: translateX(0px); }

.mm-spn ul {
  -webkit-overflow-scrolling: touch;
  top: 0;
  left: 100%;
  bottom: 0;
  z-index: 2;
  width: 130%;
  padding-right: 30%;
  line-height: 24px;
  line-height: var(--mm-spn-line-height);
  overflow: visible;
  overflow-y: auto;
  background: inherit;
  transition: left 0.3s ease 0s;
  cursor: default; }

.mm-spn > ul {
  left: 0; }

.mm-spn ul.mm-spn--open {
  left: 0; }

.mm-spn ul.mm-spn--parent {
  left: -30%;
  overflow-y: hidden; }

.mm-spn li {
  position: relative;
  background: inherit;
  cursor: pointer; }

.mm-spn .mobile-menu__item ul {
  padding-left: 20px; }

.mm-spn hr {
  width: 90%;
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  margin: 1em 0;
  padding: 0;
  margin-left: auto;
  margin-right: auto; }

.mm-spn a.mobile-menu__link,
.mm-spn span {
  position: relative;
