  margin-top: calc( (var(--mm-spn-item-height) - var(--mm-spn-line-height)) / 2);
  margin-bottom: calc( (var(--mm-spn-item-height) - var(--mm-spn-line-height)) / 2);
  padding-left: var(--mm-spn-item-indent);
  padding-right: var(--mm-spn-item-indent); }
  .mm-spn a.mobile-menu__link.phone,
  .mm-spn span.phone {
    color: #243970;
    font-size: 16px;
    font-weight: 700; }

.mm-spn a.mobile-menu__link {
  background: inherit;
  color: inherit;
  text-decoration: none; }

.mm-spn span {
  background: transparent; }

.mm-spn.mm-spn--light {
  color: #fff;
  background: #00a2dd; }

.mm-spn.mm-spn--dark {
  color: #ddd;
  background: #333; }

.mm-spn.mm-spn--vertical {
  overflow-y: auto; }

.mm-spn.mm-spn--vertical ul {
  width: 100%;
  padding-right: 0;
  position: static; }
  .mm-spn.mm-spn--vertical ul ul {
    display: none;
    padding-left: 20px;
    padding-left: var(--mm-spn-item-indent); }
    .mm-spn.mm-spn--vertical ul ul:after {
      height: 25px;
      height: calc(var(--mm-spn-item-height) / 2); }
  .mm-spn.mm-spn--vertical ul.mm-spn--open {
